/*------------------------------required fields-------------------------------*/
label .required,
.two-columns-description .required {
  box-sizing: content-box;
  color: transparent;
  height: 5px;
  width: 5px;
  left: 100%;
  bottom: 9px;
  border-radius: 50%;
  background: #f1563c;
  position: absolute;
  margin: 0 0 2px 8px;
  transition: all 0.02s;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.no-touch label .required:hover {
  height: 9px;
  width: 9px;
  margin: 0 0 0 6px;
  cursor: pointer;
}

label.unfocus-label .required {
  height: 5px;
  width: 5px;
  bottom: 9px;
  margin: 3px 0 3px 8px;
}

.no-touch label.unfocus-label .required:hover {
  margin: 0 0 1px 6px;
}

.required:after {
  position: absolute;
  text-align: center;
  content: 'Required field';
  color: #fff;
  background: #333;
  z-index: -10;
  top: -45px;
  left: -2000px;
  padding: 7px;
  border-radius: 0;
  width: 115px;
  font-size: 16px;
  line-height: 1;
  opacity: 0;
  transition: opacity 0.2s;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.required:before {
  position: absolute;
  content: '';
  border: 7px solid transparent;
  border-top-color: #333;
  z-index: -10;
  top: -16px;
  left: -2000px;
  width: 1px;
  opacity: 0;
  transition: opacity 0.1s;
}

.no-touch .required:hover:after,
.no-touch .required:hover:before {
  z-index: 2;
  opacity: 1;
}

.no-touch .required:hover:after {
  left: 0;
}

.no-touch .required:hover:before {
  left: 50%;
  transform: translateX(60%);
}

