/*----------------------------------------------------------------------------------*/
/*-------------------------------------HEADER---------------------------------------*/
/*----------------------------------------------------------------------------------*/
.launch-page {
  overflow-x: hidden;
}
.main-header {
  background:#f3f3f3;
  background-image: url(../images/home/header_bg.jpg);
  background-position: center;
  background-size: cover;
  position:relative;
  display: flex;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  height: 100vh;
  overflow: hidden;
  box-sizing: border-box;
  padding: 27px 27px 87px;
}

/*!*-----------------------------------------------------------------------------------*!*/
/*--------------------WRAPPING BG VIDEO DEPENDING OF SCREEN DIMENSIONS---------------*/
/*-----------------------------------------------------------------------------------*/

.main-header .bg-video-wrapper {
  position: absolute;
  top:0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
}
.main-header .bg-video-wrapper .bgvideo {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}
/*-----------------------------------------------------------------------------------*/
/*--------------------------------FIRST SECTION STYLES-------------------------------*/
/*-----------------------------------------------------------------------------------*/

.main-header .center {
  width:100%;
  height: auto;
  margin:0 auto;
  text-align: center;
  color: #494949;
  position:relative;
  overflow:hidden;
  box-sizing: border-box;
  display: flex;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  flex-direction: column;
  align-self: center;
  -ms-flex-item-align: center;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  -ms-flex-align: center;
  -webkit-justify-content: space-between;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
.main-header .center .main-logo {
  display: block;
  width:223px;
  height:114px;
  margin: 30px auto 50px;
  cursor: pointer;
}
.main-header .center .main-logo img,
.main-header .center .main-logo svg,
.main-header .bottom-controls-wrapper .arrow img,
.main-header .bottom-controls-wrapper .arrow svg {
  display:block;
  width:100%;
  height:100%;
  margin: 0 auto;
}
.main-header .center .header-wrapper {
  height: 64px;
  width: 100%;
  margin: 0 auto;
  position: relative;
}
.main-header .center h1,
.main-header .center p {
  user-select: none;
  -webkit-user-select: none;
  padding: 0 25px;
}
.main-header .center h1 {
  width: auto;
  max-width: 1200px;
  font-size: 88px;
  font-weight:500;
  line-height: 1;
  margin: 95px auto 30px;
}
.main-header .center h1 span.blue-color {
  color: #57a0b8;
}
.main-header .center p {
  font-size:24px;
  line-height: 1.45;
  font-weight:400;
  max-width: 660px;
  position:relative;
  margin: 45px auto;
}
.main-header .bottom-controls-wrapper {
  display:block;
  overflow: hidden;
  position: absolute;
  bottom: 27px;
  left: 50%;
  margin-left: -100px;
  width: 200px;
  height:60px;
}
.main-header .bottom-controls-wrapper .arrow {
  display:block;
  width:30px;
  height:30px;
  margin: 15px auto;
  overflow:hidden;
  z-index:2;
  cursor: pointer;
  opacity: .6;
}
.no-touch .main-header .bottom-controls-wrapper .arrow:hover {
  opacity: 1;
}



/*----------------------------------------------------------------------------------*/
/*----------------------------------ESTIMATE SECTION--------------------------------*/
/*----------------------------------------------------------------------------------*/

.fixed.cookie_notice {
  position: fixed;
  bottom: -500px;
  width: 100%;
  max-width: 100vw !important;
  overflow: hidden;
  z-index: 9991;
  background-color: #494949;
  opacity: 0.8;
  color: #fff;
  font-size: 12px;
  line-height: 1.25;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  transition: bottom 0.5s ease;
}

/*@media all and (max-width: 480px) {*/
/*  @supports (-webkit-touch-callout: none) {*/
