.chat-screen {
  box-sizing: border-box;
  width: 374px;
  max-height: calc(100vh - 32px);
  height: 574px;
  position: fixed;
  border: 0;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.645, 0.045, 0.355, 1);
  transition-property: opacity, top, bottom, transform;
  z-index: 9999999;
  right: 0;
  bottom: 0;
  padding: 16px;
  opacity: 0;
  visibility: hidden;
  transform: translateY(30px);

  &.screen-visible {
    opacity: 1;
    visibility: visible;
    top: auto;
    bottom: 0;
    transform: translateY(0);
  }

  &.screen-mobile {
    left: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    max-height: 100%;
    border: 0;

    .chat-screen__inner {
      border-radius: 0;
    }
  }

  &__inner {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 24px;
    font-family: 'Inter', sans-serif;
    color: #333333;
    overflow: auto;
    border-radius: 8px;
    position: relative;

    @media screen and (max-width: 1024px) and (min-height: 700px) {
      justify-content: space-around;
    }
  }

  &__close-btn {
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    background-color: transparent;
    border-radius: 5px;
    position: absolute;
    top: 10px;
    right: 16px;
    cursor: pointer;
    border: 1px solid transparent;
    transition: .2s;
    outline: none;

    @media all and (max-width: 992px){
      right: 15px;
      top: 9px;
    }

    &:hover {
      //box-shadow: rgba(0, 0, 0, 0.1) 0 4px 8px 0;
      border: 1px solid rgba(#333333, 0.1);
    }

    svg {
      @media all and (max-width: 992px) {
        width: 16px;
        height: 4px;
      }
    }
  }

  &__top {
    margin: 36px 0;
  }

  &__icon-wrap {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    overflow: hidden;
    margin: 0 auto 13px;
  }

  .chat-screen__title {
    font-family: 'Inter', sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 60px;
  }

  p {
    font-family: 'Inter', sans-serif !important;
    margin: 0;

    a {
      font-family: 'Inter', sans-serif !important;
      font-size: 16px;
      color: #2A69A3;
      transition: .2s;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &__descr {
    font-family: 'Inter', sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
  }

  .chat-screen__bottom-info {
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    margin-bottom: 10px;
  }

  &__button {
    display: block;
    background: #F1563C;
    border-radius: 6px;
    border: none;
    padding: 11px;
    text-align: center;
    width: 100%;
    max-width: 294px;
    margin: 0 auto;
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    color: #FFFFFF;
    box-sizing: border-box;
    cursor: pointer;
    transition: background-color .4s;

    &:hover {
      background-color: #de3c21;
    }
  }
}

.touch-device {
  .chat-screen {
    padding: 0;
    width: 100%;
    height: 100%;
    max-height: 100%;

    &__inner {
      border-radius: 0;
    }
  }
}



