    overflow: hidden;
  }

  .owl-carousel {
    overflow: hidden;
  }
  .responsive-header {
    width: 100%;
    padding: 0 40px;
    box-sizing: border-box;
    font-weight: 300;
    position: absolute;
    z-index: 2;
    top: 60px;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    font-size: 36px;
    .sub-header {
      font-size: 20px;
      display: block;
    }
  }
}
.owl-carousel .owl-nav {
  button.owl-prev,
  button.owl-next {
    appearance: none;
    box-shadow: none;
    display: block;
    z-index: 1000;
    background: transparent;
    color: transparent;
    border: none;
    position: relative;
    height: 30px;
    width: 30px;
    &:after {
      content: '';
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      height: 20px;
      width: 20px;
      border: 4px solid #fff;
      border-left: none;
      border-top: none;
      opacity: 0.5;
      padding: 0;
      margin: 0;
      transition: opacity 0.2s ease;
      transform-origin: center;
      box-sizing: border-box;
    }

    span {
      opacity: 0;
    }
    &.owl-prev {
      left: 0;
      float: left;
      &:after {
        transform: translate( -25%, -50%) rotate( 135deg );
      }
    }
    &.owl-next {
      right: 0;
      float: right;
      &:after {
        transform: translate( -75%, -50%) rotate( -45deg );
      }
    }
    &:hover:after {
      opacity: 1;
    }
    &:focus,
    &:active {
      outline: none;
      box-shadow: none;
    }
  }
}
.owl-carousel .owl-nav {
  position: absolute;
  top: 290px;
  left: 50%;
