.owl-dots {
  position: absolute;
  z-index: 1000;
  bottom: 10vw;
  width: 100%;
  text-align: center;
  line-height: $pager_size;
  height: 14px;
  .owl-dot {
    display: inline-block;
    width: $pager_size;
    margin: 0 15px;
    opacity: 0.5;
    -webkit-transition: opacity 100ms ease;
    transition: opacity 100ms ease;
    box-sizing: content-box;
    overflow: hidden;
    height: $pager_size;
    cursor: pointer;
    background-color: transparent;
    border: 1px solid #fff !important;
    &.active {
      background-color: #fff;
    }
    &:hover {
      opacity: 1;
    }
    &:focus,
    &:active {
      outline: none;
      box-shadow: none;
    }
  }
}
