/*}*/
.launch-page .read-more,
.launch-page .wpcf7-submit {
  box-sizing: border-box;
}

.launch-page .fixed-header {
  width: auto;
  display: inline-block;
  padding: 0;
  position: relative;
  text-align: left;
  font-size: 0;
  transition: all .3s ease-in-out;
  -webkit-transition: all .3s ease-in-out;
}

.fixed-header.top-fixed-header,
.launch-page .top-fixed-header {
  position: fixed;
  right: 0;
  left: 0;
  top: 0;
  text-align: center;
}

.fixed-header ul {
  width: auto;
}

.fixed-logo-wrapper {
  width: auto;
  overflow: hidden;
}

.launch-page .fixed-header .fixed-logo-wrapper {
  transition: all .2s;
  -webkit-transition: all .2s;
}

.launch-page .fixed-header .fixed-logo-wrapper {
  display: none;
}

.fixed-header .fixed-logo-wrapper,
.launch-page .fixed-header.top-fixed-header .fixed-logo-wrapper {
  display: block;
  width: 240px;
}

.fixed-header .logo {
  height: 60px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  vertical-align: middle;
  line-height: 60px;
  display: flex;
  flex-direction: row;
}

.fixed-header .logo img,
.fixed-header .logo svg {
  height: 60px;
}

.totally-fixed-logo {
  width: 100%;
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

@media all and (min-width: 1201px) {
  .totally-fixed-logo {
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
    padding: 13px;
    background-color: white;
    box-sizing: border-box;
    opacity: 1;
    transition: opacity 0.2s 0.01s ease-in-out;
  }
  .fixed-logo {
    transform: scale(0.45);
    transition: transform 0.2s ease-in-out;
  }
  .hide-full-logo .totally-fixed-logo {
    opacity: 0;
  }
  .hide-full-logo .fixed-logo {
    transform: scale(1);
  }
}

.fixed-header .fixed-list {
  display: inline-block;
  width: auto;
  //margin-right: -0.2em;
  margin-right: 1px;
}

.fixed-header ul:after {
  display: block;
  content: '';
  width: 100%;
  height: 0;
  clear: both;
}

.fixed-header ul > li {
  float: left;
  vertical-align: middle;
  position: relative;
  z-index: 3;
}

.fixed-header li.with-submenu > a {
  cursor: default;
}

/*.with-submenu:focus ul.sub-menu,*/
.with-submenu:focus-within ul.sub-menu {
  // display: block !important; // problem with clicking on the menu (does not hide automatically, desktop)
  opacity: 1 !important;
}

li.with-submenu > a:after {
  content: '';
  display: inline-block;
  width: 3px;
